import Link from 'next/link'
import { Project } from '../data/projects'
import ComplexityTag from './ComplexityTag'

type PoiItem = {
  key: 'Problem' | 'Intervention' | 'Outcome'
  value: string
}

/**
 * Derives a 3-line POI summary from project metrics (or falls back to shortDescription).
 * Metrics[0] = Problem context, [1] = Intervention, [2] = Outcome.
 */
function getPoi(project: Project): PoiItem[] {
  if (project.metrics && project.metrics.length >= 3) {
    return [
      { key: 'Problem',      value: project.metrics[0].value },
      { key: 'Intervention', value: project.metrics[1].value },
      { key: 'Outcome',      value: project.metrics[2].value },
    ]
  }
  return []
}

export default function ProjectCard({ project }: { project: Project }) {
  const poi = getPoi(project)

  return (
    <Link
      href={`/work/${project.slug}`}
      className="project-card block relative group"
      aria-label={`View case study: ${project.title}`}
    >
      {/* Image */}
      <div className="project-card-image relative">
        <img
          src={project.thumbnail || project.image || '/images/portfolio/placeholder.svg'}
          alt=""
          aria-hidden="true"
          className="w-full h-44 object-cover"
          loading="lazy"
        />
        {/* Hover overlay */}
        <div className="card-overlay" aria-hidden="true">
          <span className="overlay-cta">View case study</span>
        </div>
      </div>

      {/* Content */}
      <div className="project-card-content">
        <div className="project-card-category">{project.category}</div>
        <h3 className="project-card-title">{project.title}</h3>
        <p className="project-card-desc line-clamp-2">{project.shortDescription}</p>

        {/* Tags */}
        {project.complexityTags && project.complexityTags.length > 0 && (
          <div
            className="mt-3 flex flex-wrap gap-1.5"
            role="list"
            aria-label="Complexity tags"
          >
            {project.complexityTags.slice(0, 3).map(t => (
              <ComplexityTag key={t}>{t}</ComplexityTag>
            ))}
          </div>
        )}
      </div>

      {/* POI strip */}
      {poi.length > 0 && (
        <div className="project-card-poi" aria-label="Problem / Intervention / Outcome">
          {poi.map(row => (
            <div key={row.key} className="poi-row">
              <span className="poi-label">{row.key}</span>
              <span className="poi-value line-clamp-2">{row.value}</span>
            </div>
          ))}
        </div>
      )}
    </Link>
  )
}

